@import '~bootstrap/scss/bootstrap';

a {
    text-decoration: none !important;
    color: #00157f !important;
  }
  
  a:hover {
    color: #fd7e14 !important;
  }
  
  .font-fls {
    font-family: "Open Sans", sans-serif;
  }
  .text-fls {
    color: #00157f;
  }
  .font-montbold {
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
  }
  
  #nav-home {
    transition: all 450ms;
  }
  
  /* Landing Page */
  
  .intro {
    height: 100vh;
    background: url("https://media.discordapp.net/attachments/1140087321039999024/1177333587633262652/Captura_de_pantalla_2023-11-18_135512.png?ex=65722059&is=655fab59&hm=2305dd09b72c02d4fbed55e5adf04ca97163e506662082f57b0da78f9fb2eb8d&=&format=webp");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% center;
  }
  
  .title_container > h1 {
    font-family: "Montserrat", sans-serif;
    font-weight: 900;
    font-size: 4rem;
  }
  .subtitle_container > h2 {
    font-weight: 800;
  }
  
  .button_register > button {
    color: #fd7e14;
    border: #fd7e14 4px solid;
    border-radius: 5px;
    background-color: transparent;
    font-weight: 800;
    width: 30%;
    padding: 15px 0px;
    font-family: "Montserrat", sans-serif;
  }
  
  .button_register > button:hover {
    background: #fd7e14;
    color: #fff;
  }
  
  @media screen and (max-width: 800px) {
    .home_container {
      align-content: center;
    }
  
    .title_container > h1 {
      font-size: 2rem;
    }
  
    .home_container > div:first-child {
      width: 100% !important;
    }
    .button_register > button {
      width: 100%;
    }
  }
  
  .statsCards__container {
    display: grid;
    gap: 3rem;
    grid-template-columns: 1fr 1fr 1fr;
    place-items: center;
  }
  
  @media screen and (max-width: 1200px) {
    .statsCards__container {
      grid-template-columns: 1fr 1fr;
    }
  }
  
  @media screen and (max-width: 760px) {
    .statsCards__container {
      grid-template-columns: 1fr;
      width: 100%;
    }
  }
  
  .staff__title,
  .statistics__title {
    font-family: "Montserrat", sans-serif;
    font-weight: 900;
    font-size: 2.5rem;
    color: #00157f;
  }
  
  .staffCard__container {
    display: grid;
    gap: 5px;
    grid-template-columns: 1fr 1fr 1fr;
  }
  
  .staffCard__item {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  
  .staffCard {
    width: 100%;
    height: 80%;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    background-color: #000a54;
    border-radius: 35px;
    color: #fff;
    padding: 15px;
    font-weight: 900;
    font-family: "Montserrat", sans-serif;
    position: relative;
  }
  
  .staffCard__image {
    position: relative;
    top: 1rem;
    z-index: 10;
  }
  
  .staffCard__image > img {
    width: 120px;
    border: solid 3px #fff;
  }
  
  .staffCard__ivaoCard {
    max-width: 150px;
  }
  
  @media screen and (max-width: 1200px) {
    .staffCard__container {
      grid-template-columns: 1fr 1fr;
    }
  }
  
  @media screen and (max-width: 760px) {
    .staffCard__container {
      grid-template-columns: 1fr;
      width: 100%;
    }
  }
  
  .card {
    border-radius: 30px !important;
  }
  
  .card-img-top {
    border-top-left-radius: 30px !important;
    border-top-right-radius: 30px !important;
  }
  
  .fleet_container {
    justify-items: center;
  }
  
  .fleet__title {
    color: #00157f;
    font-weight: 900;
  }
  
  /*
  *DASHBOARD FLS
  */
  
  .row-dashboard {
    margin-bottom: 2rem;
  }
  
  .dashboard-card {
    border-radius: 1rem !important;
  }
  
  .dashboard-card-footer {
    margin-top: 0 !important;
  }
  
  .dashboard-card-body {
    background-color: #00157f;
    border-radius: inherit;
    color: white;
    padding: 3rem 0 0.5rem 0 !important;
  }
  
  .dashboard-icon-margin {
    margin-bottom: 0.5rem;
    font-size: 3rem;
  }
  
  .dashboard-text-margin {
    margin-bottom: 0;
  }
  
  .color-white {
    color: white;
  }
  
  .dashboard-pirep-card {
    background-color: #00157f !important;
  }
  
  .dashboard-img-pirep-logo {
    max-width: 125px !important;
  }
  
  .dashboard-row-pirep {
    align-self: center;
    min-width: 100%;
    place-content: center;
    margin-bottom: 1.5rem;
  }
  
  .dashboard-primary-text {
    color: white;
    text-align: center;
    font-family: "Montserrat", sans-serif;
    font-weight: 800;
    font-size: 2rem;
  }
  
  .dashboard-secondary-text {
    color: white;
    text-align: center;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 1.4rem;
  }
  
  .dashboard-icon-size {
    font-size: 2rem;
  }
  
  .color-fls {
    color: #00157f !important;
  }
  
  .news-card {
    border-color: #00157f !important;
    border-width: 3px !important;
    border-radius: 1rem !important;
  }
  
  .button-blue-fls {
    background: #00157f !important;
    color: white !important;
    font-weight: 700 !important;
    padding: 0.12rem 0.25rem 0.12rem 0.25rem !important;
    border-radius: 1rem !important;
  }
  
  .button-blue-fls:hover {
    background: #fd7e14 !important;
  }
  
  /* Jumpseat card */
  
  .select2-container--default .select2-selection--single {
    background-color: #fff;
    border: 1px solid #aaa;
    border-radius: 15px;
  }
  
  .text-orange {
    -webkit-text-stroke: 0.02rem #fd7e14;
  }
  
  .a-fls{
    transition: all 300ms;
  }
  
  .a-fls:hover{
    text-shadow: 0px 4px 10px rgba(255,255,255,0.6);
    color: #fff!important;
  }
